.App {
  font-weight: bold;
  font-family: "Bradley Hand", cursive;
  overflow-y: hidden;
}

.BMenu {
  padding: 2rem 2rem;
  width: auto;
  cursor: pointer;
}

.mapLink {
  display: flex;
  margin: 2rem 2rem;
  color: white;
  transition:0.7s;
}
.mapLink:hover{
   background-color: #000;
     color: #fff;
     opacity: .6;
     border:1px solid black;
     border-radius:5px;
}

.dropLink {
  display: block;
  color: white;
  padding: 2rem;
  border-top: 1px solid white;
}

.MenuWrapper{
  display:flex;
  justify-content:space-between;
  overflow:hidden;
}
.MenuLHS{
  padding: 8px 0px;
}
.MenuDiv{
  padding:1rem 1rem;
}
.MenuDv{
  padding:0.13rem 2rem;
}

@keyframes zoom {
  0%{
    transform:translateY(-100%);
  }
  100%{
    transform:translateY(0%);
  }
}

@media only screen and (max-width: 1500px) {
  image{ 
    background-color: black;
    opacity: .25;
  }
}

.MapMenu{
  color:white;
  font-family: "Bradley Hand", cursive;
  padding:0 2rem;
}
.MapMinMenu{
  color:white;
  display:flex;
  flex-direction: column;
  align-items: center;
  padding:0.8rem 0;
  box-sizing: border-box;
  box-shadow: black;
}
